<template>
	<div class="theme-dashboard-view">
		<!-- {{ order }} -->
		<!-- Loading -->
		<div
			class="py-5 d-flex justify-content-center align-items-center"
			v-if="!display"
		>
			<LoadingIndicator :active="loading" />
			<div v-show="load_error">
				<div
					class="justify-content-center text-center text-danger my-4"
				>
					<h4>{{ load_error }}</h4>
				</div>
			</div>
		</div>

		<!-- Main content -->
		<div v-if="display">
			<!-- Display warning if on-site but delivering status -->
			<div
				class="bg-warning text-white p-4"
				v-show="order.type == 0 && order.status == 1"
			>
				<h4>Misconfiguration Warning</h4>
				Order should not be status <strong>Delivering</strong> when
				order type is <strong>On-Site Pickup</strong>!
			</div>
			<div class="row my-5">
				<!-- ORDER STATUS -->
				<div class="col-md">
					<div
						class="px-5"
						@click="confirmUpdateStatus"
						style="cursor: pointer"
					>
						<div>
							<div
								class="
									text-light
									d-flex
									align-items-center
									justify-content-center
									rounded
								"
								:class="{
									'bg-warning': order.status == 0,
									'bg-primary': order.status == 1,
									'bg-success': order.status == 2,
								}"
								style="min-height: 32vh"
							>
								<div
									class="text-center"
									v-show="order.status == 0"
								>
									<h3>
										<span v-show="order.type == 1">{{
											$t("single_order_status_waiting")
										}}</span>
										<span v-show="order.type == 0">{{
											$t(
												"single_order_status_waiting_pickup"
											)
										}}</span>
									</h3>
									<i class="fas fa-clock"></i>
								</div>
								<div
									class="text-center"
									v-show="order.status == 1"
								>
									<h3>
										{{
											$t("single_order_status_delivering")
										}}
									</h3>
									<i class="fas fa-truck"></i>
								</div>
								<div
									class="text-center"
									v-show="order.status == 2"
								>
									<h3>
										{{
											$t("single_order_status_delivered")
										}}
									</h3>
									<i
										class="fa fa-check-circle"
										aria-hidden="true"
									></i>
								</div>
							</div>
						</div>
					</div>
					<ConfirmationDialog
						ref="ConfirmDialog"
						@confirm="updateStatus"
						:message="`${$t(
							'order_home_confirm_change_status_first'
						)} ${getOrderStatus(order.status)} ${$t(
							'order_home_confirm_change_status_middle'
						)} ${getOrderStatus(getNextStatus())} ${$t(
							'order_home_confirm_change_status_end'
						)}?`"
					/>
				</div>

				<!-- ORDER TIME -->
				<div class="col-md d-flex flex-column">
					<div
						class="h-100 d-flex flex-column justify-content-center"
					>
						<div>{{ $t("single_order_time_text") }}:</div>

						<div class="px-4">
							<h3>{{ elapsedTime }}</h3>
						</div>
					</div>
					<div class="d-flex justify-content-around p-3">
						<button class="btn btn-danger" @click="deleteOrder">
							{{ $t("single_order_delete_button") }}
						</button>

						<!-- Options The AssignDriver Popup -->
						<button
							class="btn btn-warning"
              v-if="this.order.type == 1 && this.order.status == 0"
							@click="ShowAvailableDrivers"
						>
							{{ $t("single_order_assign_to_driver_button") }}
						</button>

						<!-- Assign Driver Popup -->
						<AssignDriver ref="AssignDriver" :order_id="order.id" />
					</div>
				</div>
			</div>
			<!-- Order detailed information -->
			<div class="my-2 p-3">
				<!-- ORDER ID -->
				<div class="my-2">
					<h2>
						{{ $t("single_order_title") }}
					</h2>
				</div>

				<div class="my-3 d-flex flex-wrap">
					<!-- Map to show delivery location -->
					<div class="col-lg-6 col-12 p-2" v-show="order.type == 1">
						<Map
							:center="{
								lat: order.delivery_location.latitude,
								lng: order.delivery_location.longitude,
							}"
							:markers="[
								{
									position: {
										lat: order.delivery_location.latitude,
										lng: order.delivery_location.longitude,
									},
								},
							]"
						/>
					</div>

					<!-- Order textual information -->
					<div class="col-lg-6 col-12">
						<InfoBox
							:title="$t('single_order_id_text')"
							:data="order.id"
						></InfoBox>

						<InfoBox
							:title="$t('single_order_type_text')"
							:data="order.type"
						>
							<template v-slot:data="params">
								<div>
									<span>
										{{ getOrderType(params.data) }}
									</span>
								</div>
							</template>
						</InfoBox>
						<InfoBox
							:title="$t('single_order_user_phone_number_text')"
							:data="order.user_phoneNumber"
						>
						</InfoBox>
						<InfoBox
							:title="$t('single_order_payment_option_text')"
							:data="order.payment_option"
						>
							<template v-slot:data="params">
								<div>
									<span v-show="params.data == 0">
										Telebirr
									</span>
									<span v-show="params.data == 1">
										CBEBirr
									</span>
								</div>
							</template>
						</InfoBox>

						<InfoBox
							:title="$t('single_order_payment_status_text')"
							:data="order.payment_status"
						>
							<template v-slot:data="params">
								<div>
									<span v-show="params.data">
										<i
											class="
												fa fa-check-circle
												text-success
											"
										></i>
										Paid</span
									>
									<span v-show="!params.data">
										<i
											class="
												fa fa-times-circle
												text-danger
											"
											aria-hidden="true"
										></i>
										Not Paid
									</span>
								</div>
							</template>
						</InfoBox>

						<InfoBox
							:title="$t('single_order_branch_text')"
							:data="order.branch"
						>
							<template v-slot:data="params">
								<div>
									<span v-show="params.data == 1">Nani</span>
									<span v-show="params.data == 2"
										>Almoudi</span
									>
									<span v-show="params.data == 3">Sara</span>
								</div>
							</template>
						</InfoBox>
					</div>
				</div>

				<!-- ORDER ITEMS -->
				<div class="p-3">
					<h3>{{ $t("single_order_list_title") }}</h3>
				</div>
				<!-- {{order_items}} -->
				<div class="border">
					<!-- <Table
						:rows="order_items"
						:contain="['id', 'quantity', 'price', 'name']"
					>
						<template v-slot:id-title>{{
							$t("single_order_list_id")
						}}</template>
						<template v-slot:quantity-title>{{
							$t("single_order_list_quantity")
						}}</template>
						<template v-slot:price-title>{{
							$t("single_order_list_price")
						}}</template>

						<template v-slot:name-title>{{
							$t("single_order_list_name")
						}}</template>
					</Table> -->
					<hr />
					<div class="mx-3">
						<h5>
							{{ $t("single_order_total_price") }}:
							{{ order.total_price }} ETB
						</h5>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
// Components
import Table from "@/components/Table.vue";
import LoadingIndicator from "@/components/LoadingIndicator.vue";
import ConfirmationDialog from "@/components/ConfirmationDialog.vue";
import Map from "@/components/Map.vue";
import AssignDriver from "@/components/AssignDriver.vue";
import InfoBox from "@/components/InfoBox.vue";
//Services
import OrderService from "@/services/order.js";

export default {
	name: "SingleOrder",
	components: {
		Table,
		LoadingIndicator,
		ConfirmationDialog,
		Map,
		AssignDriver,
		InfoBox,
	},
	data() {
		return {
			order: {},
			order_items: [],
			loading: false,
			load_error: "",
			display: false,
			interval: null,
			elapsedTime: null,
      showAssignButton: false,
			// Available Drivers
			availableDrivers: [],
		};
	},
	methods: {
		ShowAvailableDrivers() {
			this.$refs.AssignDriver.ShowAvailableDriversPopup();
		},

		DateParse(dateString) {
			const date = new Date(Date.parse(dateString));
			return date;
		},

		CalculateTimeDifference(date1, date2) {
			var difference = date1.getTime() - date2.getTime();
			var milliseconds = Math.floor((difference % 1000) / 100);
			var seconds = Math.floor((difference / 1000) % 60);
			var minutes = Math.floor((difference / (1000 * 60)) % 60);
			var hours = Math.floor((difference / (1000 * 60 * 60)) % 24);
			var days = Math.floor(difference / (1000 * 60 * 60 * 24));

			var result = `${days} ${this.$t(
				"single_order_time_days"
			)} : ${hours} ${this.$t(
				"single_order_time_hours"
			)} : ${minutes} ${this.$t(
				"single_order_time_minutes"
			)} : ${seconds} ${this.$t("single_order_time_seconds")} `;
			return result;
		},
		startClock(time) {
			// Update the time every second
			this.interval = setInterval(() => {
				this.elapsedTime = this.CalculateTimeDifference(
					new Date(),
					this.DateParse(time)
				);
			}, 1000);
		},

		getOrderType(index) {
			switch (index) {
				case 0:
					return this.$t("single_order_type_on_site");
				case 1:
					return this.$t("single_order_type_delivery");
			}
		},
		getOrderStatus(index) {
			switch (index) {
				case 0:
					return this.$t("order_home_table_status_pending");
				case 1:
					return this.$t("order_home_table_status_delivering");
				case 2:
					return this.$t("order_home_table_status_delivered");
			}
		},
		getNextStatus() {
			// If type is  site pick up
			// console.log("Next Status", this.order.type, this.order.status);
			if (this.order.type == 0) {
				// If pending switch to delivered
				if (this.order.status == 0) {
					return 2;
					// IF delivered switch to pending
				} else if (this.order.status == 2) {
					return 0;
				}
				// If there is an error and it becomes delivering switch to pending
				else if (this.order.status == 1) {
					return 0;
				}
				// If type is delivery
			} else if (this.order.type == 1) {
				// If pending switch to delivering
				if (this.order.status == 0) {
					return 1;
					// If delivering switch to delivered
				} else if (this.order.status == 1) {
					return 2;
					// If delivered switch to pending
				} else if (this.order.status == 2) {
					return 0;
				}
			}
		},
		updateStatus() {
			this.loading = true;
			this.load_error = "";

			OrderService.updateStatus(this.order.id, this.getNextStatus()).then(
				(response) => {
					this.loading = false;
					this.order.status = this.getNextStatus();
				},
				(error) => {
					console.log(error.response);
					this.loading = false;
					this.load_error =
						(error.response &&
							error.response.data &&
							error.response.data.message) ||
						error.message ||
						error.toString();
				}
			);
		},
		deleteOrder() {
			this.loading = true;
			this.load_error = "";
			OrderService.deleteOrder(this.$route.params.id).then(
				(response) => {
					this.loading = false;
					this.$router.push("/orders");
				},
				(error) => {
					this.loading = false;
					console.log("Error:", error);
					this.load_error =
						(error.response &&
							error.response.data &&
							error.response.data.message) ||
						error.message ||
						error.toString();
				}
			);
		},
		confirmUpdateStatus() {
			this.$refs.ConfirmDialog.showConfirmDialog();
		},
	},
	beforeUnmount() {
		// prevent memory leak
		clearInterval(this.interval);
	},
	mounted() {
		this.loading = true;
		this.load_error = "";
		OrderService.getSingleOrder(this.$route.params.id).then(
			(response) => {
				this.loading = false;
				this.display = true;
				console.log(response.data.data);
				console.log(response.data.data.items);
				this.order = response.data.data;
				this.order_items = response.data.data.items;
        if(this.order.type === 1 && this.order.status === 0) {
          console.log(`show assign button is:  ${this.showAssignButton}`);
          this.showAssignButton = true;
          console.log(`show assign button is:  ${this.showAssignButton}`);
        };
				if (this.order.status != 2) {
					this.startClock(this.order.created_at);
				} else {
					this.elapsedTime =
						this.DateParse(this.order.finished_at) -
						this.DateParse(this.order.created_at);
				}
			},
			(error) => {
				this.loading = false;
				console.log("Error:", error);
				this.load_error =
					(error.response &&
						error.response.data &&
						error.response.data.message) ||
					error.message ||
					error.toString();
			}
		);
	},
};
</script>

