<template>
	<div class="table-responsive">
		<table class="table" style="border: none">
			<thead>
				<tr>
					<th
						scope="col"
						v-for="(col, index) in columns"
						:key="index"
						class="bg-light"
						style="padding: 2vh"
						v-show="containColumn.includes(col)"
					>
						<slot :name="col + '-title'" :col="col">
							{{ col }}
						</slot>
					</th>
					<th
						v-if="functions.length > 0 && rows.length > 0"
						style="padding: 2vh"
					>
						Functions
					</th>
				</tr>
			</thead>
			<tbody style="border: none">
				<tr
					v-for="(row, index) in rows"
					:key="index"
					:class="hover ? 'hover' : ''"
					@click="rowClickEvent(index)"
				>
					<td
						v-for="(col, index) in columns"
						:key="index"
						style="padding: 2vh"
						v-show="containColumn.includes(col)"
					>
						<!-- {{ row[col] }} -->
						<slot :name="col" :row="row">
							{{ row[col] }}
						</slot>
					</td>
					<!-- Row Functions  -->
					<div v-if="functions.length > 0" style="padding: 0">
						<td
							v-for="(functs, func_index) in functions"
							:key="func_index"
							style="padding: 2vh"
						>
							<button class="btn p-2">{{ functs.text }}</button>
						</td>
					</div>
				</tr>
			</tbody>
		</table>
	</div>
</template>

<script>
export default {
	name: "Table",
	props: {
		hover: {
			type: Boolean,
			default: false,
		},
		rows: Array,
		functions: {
			type: Array,
			default: [],
		},
		isHeader: {
			type: Boolean,
			default: true,
		},
		contain: {
			type: Array,
			default: [],
		},
		innerRow: {
			type: Array,
			default: [],
		},
		rowClickFunction: {
			type: Function,
			default: () => {},
		},
	},
	computed: {
		// Conver the row keys into column names
		columns: function columns() {
			if (this.rows.length == 0) {
				return [];
			}
			return Object.keys(this.rows[0]);
		},

		// Column Filtering
		// limit to the ones in contain array
		containColumn() {
			if (this.rows.length == 0) {
				return this.contain;
			}
			return this.contain.length != 0
				? this.contain
				: Object.keys(this.rows[0]);
		},
	},
	methods: {
		// If a function button is clicked
		functionEvent(index) {
			if (this.functions[index].event) {
				return this.functions[index].event;
			} else {
				return null;
			}
		},

		// If a row is clicked
		rowClickEvent(index) {
			// console.log("Clicked Row:", index);
			this.rowClickFunction(index);
		},
	},
};
</script>

<style scoped>
th {
	border-bottom: 1px lightgray solid;
}

td {
	border: none;
}

.hover:hover {
	background-color: #eee;
	cursor: pointer;
}
.hover:active {
	background-color: #ddd;
}
</style>