<template>
	<div>
		<GMapMap
			:center="center"
			:zoom="15"
			map-type-id="terrain"
			:style="{ height: height + 'px' }"
		>
			<GMapCluster>
				<GMapMarker
					:key="index"
					v-for="(m, index) in markers"
					:position="m.position"
					:clickable="true"
					:draggable="true"
					@click="center = m.position"
				/>
			</GMapCluster>
		</GMapMap>
	</div>
</template>


<script>
export default {
	name: "Map",
	props: {
		center: {
			type: Object,
			default: { lat: 8.9806034, lng: 38.7577605 },
		},
		markers: {
			type: Array,
			default: [],
		},
		height: {
			type: Number,
			default: 400,
		},
	},
	methods: {},
};
</script>